"use client";

import Link from "next/link";
import { FiChevronDown } from "react-icons/fi";
import { HiOutlineSupport } from "react-icons/hi";

export default function Navbar() {
  return (
    <header className="lek-navbar shadow-sm bg-white">
      <div className="container d-flex align-items-center justify-content-between py-2">
        {/* left: logo */}
        <div className="lek-logo">
          <Link href="/" legacyBehavior>
            <a>
              <img src="/img/logo.png" alt="Logo" />
            </a>
          </Link>
        </div>

        {/* center: nav (desktop only) */}
        <nav className="lek-nav-links d-none d-md-flex align-items-center justify-content-center">
          <Link href="/" legacyBehavior>
            <a className="lek-nav-item active">
              Home <FiChevronDown size={14} />
            </a>
          </Link>
          <Link href="/shop" legacyBehavior>
            <a className="lek-nav-item">
              Shop <FiChevronDown size={14} />
            </a>
          </Link>
          <Link href="/pages" legacyBehavior>
            <a className="lek-nav-item">
              Pages <FiChevronDown size={14} />
            </a>
          </Link>
          <Link href="/elements" legacyBehavior>
            <a className="lek-nav-item">
              Elements <FiChevronDown size={14} />
            </a>
          </Link>
          <Link href="/blog" legacyBehavior>
            <a className="lek-nav-item">
              Blog <FiChevronDown size={14} />
            </a>
          </Link>
        </nav>

        {/* right: support */}
        <div className="lek-support d-none d-md-flex align-items-center">
          <div className="lek-support-icon">
            <HiOutlineSupport size={28} />
          </div>
          <div className="lek-support-text ms-2">
            <div className="lek-support-small">24/7 Customer Support:</div>
            <div className="lek-support-phone fw-bold">+1 800 559 6580</div>
          </div>
        </div>

        {/* NOTE: mobile hamburger / menu goes here if you want it later */}
      </div>
    </header>
  );
}
